import React from 'react';
import { Link } from 'react-router-dom';
import { generate as uniKey } from 'shortid';
import { productList } from '../../data/productList';
import './style.scss';

function Navigator() {
	return (
		<header className='header'>
			<nav className='header__nav'>
				{/* <a className='header__logo'> */}
				<Link className='header__logo' to='/web-ryp'>
					<img
						src='data:image/jpeg;base64,'
						alt='logo'
					/>
				</Link>
				{/* </a> */}

				<ul className='header__ul'>
					<li key={uniKey()}>
						<Link
							className='nav-item'
							to='/web-ryp'>
							ALL
						</Link>
					</li>
					{getItemsMenu().map(i => (
						<li key={uniKey()}>
							<Link
								className='nav-item'
								to={`/${i}`}>
								<i
									className={`bx bxs-${
										i?.toLocaleLowerCase() ===
										'bebidas'
											? 'drink'
											: 'dish'
									}`}></i>
							</Link>
						</li>
					))}
				</ul>
			</nav>
		</header>
	);
}

const getItemsMenu = _ => [...new Set(productList.map(item => item.category))];

export default Navigator;
